<template>
    <div>
        <h3>{{ title }}</h3>
        <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <b-form-group
                label="Address"
                label-for="inputAddress"
            >
                <b-form-input
                    id="inputAddress"
                    v-model="$v.form.name.$model"
                    v-bind:class="{ 'is-invalid': $v.form.name.$error }">
                ></b-form-input>
            </b-form-group>
            <b-button type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
        </b-form>
    </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators';
import Preloader from '../Preloader';
import Backend from '../../services/backend';

const backend = new Backend();
const google = window.google;

export default {
    name: 'AddressesAddEditPage',
    components: {
        Preloader
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    validations: {
        form: {
            name: {
                required
            }
        }
    },
    created() {
        this.handlerForRouteChange();
    },
    mounted() {
        this.activatePlacesSearch();
    },
    props: [
        'pageType',
        'id'
    ],
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Address Edit Page' : 'Address Add Page';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/addresses/' + id)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch((error) => {
                        this.errorFetching = true;
                        this.isFetching = false;
                        this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                            'Something went wrong. Please try again later';
                    });
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = this.form;
            if (this._props.pageType === 'add') {
                backend.postRequest('services/addresses/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.successMessage = `${name} was added successfully`;
                        this.name = '';
                        setTimeout(() => this.$v.$reset(), 0);
                    })
                    .catch((error) => {
                        this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                            'Something went wrong. Please try again later';
                    });
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/addresses/' + this.$route.params.id + '/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `${name} was changed successfully`;
                    })
                    .catch((error) => {
                        this.isSaving = false;
                        this.successMessage = null;
                        this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                            'Something went wrong. Please try again later';
                    });
            }
        },
        activatePlacesSearch() {
            let defaultBounds = new google.maps.LatLngBounds(
                new google.maps.LatLng(-33.8902, 151.1759),
                new google.maps.LatLng(-33.8474, 151.2631));

            let input = document.getElementById('inputAddress');
            let options = {
                bounds: defaultBounds,
                types: ['address']
            };

            let autocomplete = new google.maps.places.Autocomplete(input, options);
            autocomplete.addListener('place_changed', () => {
                let place = autocomplete.getPlace();
                this.form.name = place.formatted_address;
            });
        },
        clearForm() {
            this.form = {
                name: ''
            };
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
