import Vue from 'vue';
import VueRouter from 'vue-router';
import Login from '../components/Login';
import AdminPage from '../components/AdminPage';
import AddressesPage from '../components/pages/AddressesPage';
import AddressesAddEditPage from '../components/pages/AddressesAddEditPage';
import CarriersPage from '../components/pages/CarriersPage';
import CarriersAddEditPage from '../components/pages/CarriersAddEditPage';
import CircuitLinksPage from '../components/pages/CircuitLinksPage';
import CircuitSpeedsPage from '../components/pages/CircuitSpeedsPage';
import CircuitSpeedsAddEditPage from '../components/pages/CircuitSpeedsAddEditPage';
import CircuitTypesPage from '../components/pages/CircuitTypesPage';
import CircuitTypesAddEditPage from '../components/pages/CircuitTypesAddEditPage';
import CircuitsPage from '../components/pages/CircuitsPage';
import CircuitsAddEditPage from '../components/pages/CircuitsAddEditPage';
import DeviceManufacturersPage from '../components/pages/DeviceManufacturersPage';
import DeviceManufacturersAddEditPage from '../components/pages/DeviceManufacturersAddEditPage';
import DevicesPage from '../components/pages/DevicesPage';
import LocationsPage from '../components/pages/LocationsPage';
import LocationsAddEditPage from '../components/pages/LocationsAddEditPage';
import SupportOrganizationsPage from '../components/pages/SupportOrganizationsPage';
import SupportOrganizationsAddEditPage from '../components/pages/SupportOrganizationsAddEditPage';
import EmptyComponent from '../components/EmptyComponent';
import PartnersPage from '../components/pages/PartnersPage';
import PartnersAddEditPage from '../components/pages/PartnersAddEditPage';
import ClientsPage from '../components/pages/ClientsPage';
import ClientsAddEditPage from '../components/pages/ClientsAddEditPage';

Vue.use(VueRouter);

const ifNotAuthenticated = (to, from, next) => {
    if (!localStorage.token) {
        next();
        return;
    }
    next({ name: 'adminPage' });
};

const ifAuthenticated = (to, from, next) => {
    if (localStorage.token) {
        next();
        return;
    }
    next({ name: 'login' });
};

export default new VueRouter({
    routes: [
        {
            name: 'adminPage',
            path: '/',
            component: AdminPage,
            beforeEnter: ifAuthenticated,
            children: [
                {
                    path: 'services',
                    component: EmptyComponent,
                    children: [
                        {
                            name: 'addresses',
                            path: 'addresses',
                            component: AddressesPage
                        },
                        {
                            name: 'addressesAdd',
                            path: 'addresses/add',
                            component: AddressesAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'addressesEdit',
                            path: 'addresses/:id/edit',
                            component: AddressesAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'carriers',
                            path: 'carriers',
                            component: CarriersPage
                        },
                        {
                            name: 'carriersAdd',
                            path: 'carriers/add',
                            component: CarriersAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'carriersEdit',
                            path: 'carriers/:id/edit',
                            component: CarriersAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'circuitLinks',
                            path: 'circuit_links',
                            component: CircuitLinksPage
                        },
                        {
                            name: 'circuitTypes',
                            path: 'circuit_types',
                            component: CircuitTypesPage
                        },
                        {
                            name: 'circuitTypesAdd',
                            path: 'circuit_types/add',
                            component: CircuitTypesAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'circuitTypesEdit',
                            path: 'circuit_types/:id/edit',
                            component: CircuitTypesAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'circuitSpeeds',
                            path: 'circuit_speeds',
                            component: CircuitSpeedsPage
                        },
                        {
                            name: 'circuitSpeedsAdd',
                            path: 'circuit_speeds/add',
                            component: CircuitSpeedsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'circuitSpeedsEdit',
                            path: 'circuit_speeds/:id/edit',
                            component: CircuitSpeedsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'circuits',
                            path: 'circuits',
                            component: CircuitsPage
                        },
                        {
                            name: 'circuitsAdd',
                            path: 'circuits/add',
                            component: CircuitsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'circuitsEdit',
                            path: 'circuits/:id/edit',
                            component: CircuitsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'deviceManufacturers',
                            path: 'device_manufacturers',
                            component: DeviceManufacturersPage
                        },
                        {
                            name: 'deviceManufacturersAdd',
                            path: 'device_manufacturers/add',
                            component: DeviceManufacturersAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'deviceManufacturersEdit',
                            path: 'device_manufacturers/:id/edit',
                            component: DeviceManufacturersAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'devices',
                            path: 'devices',
                            component: DevicesPage
                        },
                        {
                            name: 'locations',
                            path: 'locations',
                            component: LocationsPage
                        },
                        {
                            name: 'locationsAdd',
                            path: 'locations/add',
                            component: LocationsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'locationsEdit',
                            path: 'locations/:id/edit',
                            component: LocationsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'supportOrganizations',
                            path: 'support_organizations',
                            component: SupportOrganizationsPage
                        },
                        {
                            name: 'supportOrganizationsAdd',
                            path: 'support_organizations/add',
                            component: SupportOrganizationsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'supportOrganizationsEdit',
                            path: 'support_organizations/:id/edit',
                            component: SupportOrganizationsAddEditPage,
                            props: { pageType: 'edit' }
                        }

                    ]
                },
                {
                    path: 'vbill',
                    component: EmptyComponent,
                    children: [
                        {
                            name: 'clients',
                            path: 'clients',
                            component: ClientsPage
                        },
                        {
                            name: 'clientsAdd',
                            path: 'clients/add',
                            component: ClientsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'clientsEdit',
                            path: 'clients/:id/edit',
                            component: ClientsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'partners',
                            path: 'partners',
                            component: PartnersPage
                        },
                        {
                            name: 'partnersAdd',
                            path: 'partners/add',
                            component: PartnersAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'partnersEdit',
                            path: 'partners/:id/edit',
                            component: PartnersAddEditPage,
                            props: { pageType: 'edit' }
                        }
                    ]
                }
            ]
        },
        {
            name: 'login',
            path: '/login',
            component: Login,
            beforeEnter: ifNotAuthenticated
        }

    ]
});
